@tailwind base;
@tailwind components;
@tailwind utilities;

header ul li {
  @apply px-2;
}
.bg-primary {
  background-color: rgb(114, 4, 117);
}
.bg-secondary {
  background-color: rgb(72, 6, 75);
}
.mobile-nav {
  @apply fixed top-10 left-0 bg-gray-800 w-full h-1/3;
}
.mobile-nav li {
  @apply w-full h-full text-center border-b-2;
}
.project-des {
  @apply absolute left-0 right-0 bottom-0 top-0 bg-secondary opacity-0 duration-500 hover:opacity-100;
}
.btn {
  @apply bg-primary px-5 py-2 font-bold hover:border-2 rounded;
}
html {
  scroll-behavior: smooth;
}
